.main {
    background-size: cover;
    background-attachment: fixed;
    background-color: rgb(231, 231, 231);
}

.header {
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 100px;
}

.heading {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 70px;
    font-weight: bolder;
    text-transform: capitalize;
    margin-left: 50px;
    margin-right: 50px;
    color: rgb(45, 82, 117);
}

.btn {
    background: rgb(45, 82, 117);
    color: white;
    border: 2px solid black;
    border-radius: 20px;
    padding: 5px 15px;
    cursor: pointer;
    margin-right: 10px;
    margin-left: 10px;
    margin-top: 0px;
}

.btn:hover {
    background: rgb(248, 246, 246);
    color: rgb(19, 18, 18);
    border: 2px solid rgb(145, 143, 143);
    border-radius: 40px;
    padding: 5px 15px;
    cursor: pointer;
}

.header-img {
    padding: 0px;
    width: 100%;
    border: 2px solid rgb(145, 143, 143);
    border-radius: 20px;
    margin-top: 30px;
}


@media only screen and (max-width: 600px) {
    .heading {
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        font-size: 30px;
        font-weight: bolder;
        text-transform: capitalize;
    }
    
}
