.all-services {
    text-align: left;
    display: flex;
    margin-top: 30px;
}

.services-text {
    padding: 20px 50px;
}

.container {
    border: 2px solid rgb(45, 82, 117);
    border-radius: 20px;
    background: rgb(255, 255, 255);
    margin: 40px;
    padding: 20px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}

.heading1 {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 50px;
    font-weight: bold;
    text-transform: capitalize;
    margin-left: 50px;
    margin-right: 50px;
    color: rgb(45, 82, 117);
    text-align: center;
    margin-top: 40px;
}

.heading-text {
    padding: 0px 20px;
    font-size: 18px;
    text-align: center;
    margin-bottom: 100px;
}

.img {
    width: 160px;
    height: 160px;
    border-radius: 50%;
}

.mini-heading {
    font-weight: bold;
    font-size: 30px;
    color: rgb(45, 82, 117);
}

@media only screen and (max-width: 600px) {
    .services-text {
        padding: 20px 25px;
    }
}